.Container {
    width: 90%;
    margin: auto;
}

.Message {
    font-family: Vidaloka, serif;
    font-size: 20px;
}

.FormInput {
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    border: 1px solid gray;
    border-radius: 0px;
    font-size: 15px;
}

.FormSelect {
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    appearance: none;
    background-position: right 50%;
    font-size: 15px;
    border-radius: 0px;
    display: block;
    background-repeat: no-repeat;
    background-image: url(data:image/png;base64,);
}

select {
    display: block;
}

option {
    padding: 10px;
    margin-top: 7px;
    margin-bottom: 7px;
    border-radius: 0;
}